import React, { useEffect, useState, useMemo } from "react";
import Header from "../../components/header/comp";
import Footer from "../../components/footer/comp";
import { useAppContext } from "../../Application";
import { useParams } from "react-router-dom";

export default function PassportView() {
    const { setTitle, navigate } = useAppContext();
    const { oid, version } = useParams();
    const [hide, setHide] = useState(true); //   - 
    const [passportLoading, setPassportLoading] = useState(false);
    const [versionsLoading, setVersionsLoading] = useState(false);
    const [versions, setVersions] = useState([]);
    const [passportData, setPassportData] = useState(null);

    //    
    const toggleStructureNotes = () => {
        setHide(prev => !prev);
    };

    useEffect(() => {
        const fetchVersions = async () => {
            try {
                setVersionsLoading(true);
                const response = await fetch(`/nsi/versions?identifier=${oid}`);
                if (!response.ok) throw new Error(' ');
                const { data } = await response.json();
                setVersions(data);
            } catch (error) {
                console.error("   :", error);
            } finally {
                setVersionsLoading(false);
            }
        };

        setTitle(" ");
        fetchVersions();
    }, [setTitle, oid]);

    //   
    useEffect(() => {
        const fetchPassport = async () => {
            if (!version) return;

            try {
                setPassportLoading(true);
                const response = await fetch(`/nsi/passport?identifier=${oid}&version=${version}`);
                if (!response.ok) throw new Error(' ');
                const { data } = await response.json();
                setPassportData(data);
            } catch (error) {
                console.error("   :", error);
                setPassportData(null);
            } finally {
                setPassportLoading(false);
            }
        };

        fetchPassport();
    }, [oid, version]);

    const versionsOptions = useMemo(() => {
        return versions.map((item) => (
            <option key={item.version} value={item.version}>
                 {item.version}  {item.publishDate || ''}
            </option>
        ));
    }, [versions]);

    const headerInfo = useMemo(() => {
        if (passportLoading || versionsLoading) {
            return <div>...</div>;
        }
        return passportData ? (
            <>
                <h4 className="nomarg">{passportData.shortName}</h4>
                <div className="row gap">
                    <p className="mini nomarg">{passportData.oid}</p>
                    <p className="mini nomarg">{passportData.version}</p>
                    <p className="mini nomarg">: <b>{passportData.createDate}</b></p>
                    <p className="mini nomarg">: <b>{passportData.publishDate}</b></p>
                    <p className="mini nomarg">: <b>{passportData.lastUpdate}</b></p>
                </div>
            </>
        ) : (
            <p>  </p>
        );
    }, [passportLoading, versionsLoading, passportData]);

    const descriptionBlock = useMemo(() => {
        if (passportLoading || versionsLoading) {
            return <p> ...</p>;
        }
        return passportData ? (
            <pre className="nomarg">
                {passportData.description || "  "}
            </pre>
        ) : (
            <p>   </p>
        );
    }, [passportLoading, versionsLoading, passportData]);

    //   'hide'    
    const structureNotesBlock = useMemo(() => {
        if (passportLoading || versionsLoading) {
            return <p> ...</p>;
        }
        return passportData ? (
            <>
                <pre className={`nomarg structureNotes ${hide ? "hide" : ""}`}>
                    {passportData.structureNotes || "  "}
                </pre>
                <button
                    onClick={toggleStructureNotes}
                    className="small"
                >
                    {hide ? "" : ""}
                </button>
            </>

        ) : (
            <p>   </p>
        );
    }, [passportLoading, versionsLoading, passportData, hide]); //    hide

    const releaseNotesBlock = useMemo(() => {
        if (passportLoading || versionsLoading) {
            return <p> ...</p>;
        }
        return passportData ? (
            <pre className="nomarg">
                {passportData.releaseNotes || " "}
            </pre>
        ) : (
            <p>   </p>
        );
    }, [passportLoading, versionsLoading, passportData]);

    const handleVersionChange = (e) => {
        const newVersion = e.target.value;
        navigate(`/passport/${oid}/${newVersion}`);
    };

    return (
        <>
            <Header />
            <main>
                <div className="row btw ctr">
                    <div className="clm ctr">
                        <div className="row gap algctr">
                            <button onClick={() => navigate("/search")}></button>
                            <div className="clm">
                                {headerInfo}
                            </div>
                        </div>
                    </div>
                </div>
                <div className="panel btw row gap">
                    <div className="max60 clm gap">
                        <div className="clm">
                            <p className="mini"> </p>
                            {descriptionBlock}
                        </div>
                        <div className="clm">
                            <div className="row algctr gap">
                                <p className="mini"> </p>
                            </div>
                            {structureNotesBlock}
                        </div>
                    </div>
                    <div className="rightBar clm gap">
                        <div className="clm">
                            <p className="mini nomarg"> </p>
                            {versionsLoading ? (
                                <p> ...</p>
                            ) : (
                                <select
                                    value={version}
                                    onChange={handleVersionChange}
                                    disabled={versions.length === 0}
                                >
                                    {versionsOptions}
                                </select>
                            )}
                        </div>
                        <div className="clm">
                            <p className="mini nomarg"> </p>
                            {releaseNotesBlock}
                        </div>
                    </div>
                </div>
            </main>
            <Footer />
        </>
    );
}